.header {
    min-height: 300px;
    padding: 100px 20px;
    text-align: center;
    background-color:#00215b;
    color: white;
}

h1 {
    font-weight: bold;
    font-size: 2.8em;
}
.card {
    margin: 25px 8px ;
}

img {
    width: 160px;
    height: 260px;
    border: 5px solid red;
    box-shadow: 0 6px 12px
}

.card:hover {
    box-shadow: 0px 50px 100px rgba(33,33,33,.2); 
  }
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 10px;
    padding-bottom: 50px;
    margin: 0;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
background-image: linear-gradient(315deg, #d3d3d3 0%, #ee5253 74%);

  }
.navbar {
background-image: linear-gradient(315deg, #000000 0%, #b82e1f 74%);
    color: white;
    font-size: 1.5em;
    position: fixed;
    z-index: 100;
    width: 100%;
}

.navbar-brand {
    font-weight: bold;
    font-size: 2em;
    font-family: 'Pacifico', cursive;
}
.footer {
    min-height: 60px;
    text-align: center;
    background-color:#00215b;
    color: white;
    width: 100%;
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 10px;
}

.footer-link {
    color: white;
    font-weight: bold;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

